@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
.global-article {
    /* max-width: 700px; */
    margin: 20px auto 0;
    padding: 0 20px;
    background: #29344a;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
    /* display: flex; */
}

.global-data-title {
    font-size: 30px;
}

.global-data-numbers {
    font-size: 55px;
    font-weight: 800;
}
.pais-article {
    margin: 20px auto 0;
    background: #29344a;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
    transition: all 0.2s;
    font-weight: 800;
    font-size: 18px;
}

.pais-article:hover {
    transform: scale(1.06);
    cursor: pointer;
}

.bandeira {
    border-radius: 40%;
    width: 100%;
    height: 64px;
}

.bandeira-img {
    border-radius: 10px;
    width: 67px;
}

.unstyled-link {
    color: var(--dark)!important;
    text-decoration: none!important;
}
.class-teste {
    min-height: 87vh
}
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    /* font-family: Arial, Helvetica, sans-serif; */
    background: #161e2c!important;
    color: #333;
    overflow-x: hidden!important;
}

.notification {
    box-shadow: none!important;
}

.color-cases {
    color: #0ee9cb;
}

.color-death {
    color: #f35353;
}

.color-recovered {
    color: #347fd5;
}

.color-active {
    color: #ffc137;
}

.color-new-case {
    color: #ff8a37;
}

.color-new-death{
    color: #af1c1c;
}
#main-header {
    height: 60px;
    width: 100%;
    background: #222c45;
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}



.blobs-container {
	display: flex;
}

.blob {
	background: black;
	border-radius: 50%;
	box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
	margin: 10px;
	height: 20px;
	width: 20px;
	transform: scale(1);
	-webkit-animation: pulse-black 2s infinite;
	        animation: pulse-black 2s infinite;
}

@-webkit-keyframes pulse-black {
	0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
	}
	
	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
	}
	
	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
}

@keyframes pulse-black {
	0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
	}
	
	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
	}
	
	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
	}
}

